.navbar_
{
    top: 0;
    display: flex;
    flex-direction: row;
    background: #eeeeee;
    align-items: center;
    font-family: var(--font-family);
    padding: 2rem 0rem 1rem 2rem;
    justify-content: flex-start;
    
}
.navbar_-logo
{
    display: flex;
    justify-content: center;
    align-items: center;   
    cursor: pointer; 
}

.navbar_-circle
{
    background: black;
    color: #eeeeee;
    padding: 1.3rem 1rem;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 50%;
    font-family: var(--font-family);
    margin-right: 0.75rem;
}

.navbar_-title h1
{
    font-size: 1.4rem;
    font-weight: 800;
    color: var(--color-dark);
    font-family: var(--font-family);
    margin-right: 0.75rem;
    flex-wrap: wrap;
    
}

.PM__navbar-links_logo-return {
    font-size: 0.9rem;
    margin-top: 4px;
    margin-left: 2px;
    cursor: pointer;
  }
